html, body {
  height: 100%;
  overflow: hidden;
}

body {
  font-family: 'Libre Franklin', sans-serif;
  background: #000;
}

.Canvas {
    display: block;
    height: 100vh;
    width: 100vw;
    background-color: #000000;
    /* margin: 0 auto 5px auto;
    position: relative;
    border: 5px solid black; */
    position: absolute;
    left: 0;
